.wrapperDiv{
    border: 1px solid #000;
    position: absolute;
    top: 85%;
    right: 26.5rem;
    width: 30px;
    height: 30px;
    z-index: 2;
    background-color: #fff;
    transform: rotate(45deg);
    border: 3px solid rgb(12, 57, 218);
    border-bottom: none;
    border-right: none;

    animation-name: showInWrapper;
    animation-duration: 0.3s;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
}

.filterMainDiv{
    width: 100%;
    height: auto;
    position: absolute;
    top: 100%;
    background-color: white;
    border-radius: 10px;
    padding: 1.7rem 1rem;
    padding-bottom: 1.5rem;
    border: 3px solid rgb(12, 57, 218);

    animation-name: showInSimple;
    animation-duration: 0.3s;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
}

@keyframes showInWrapper {
    0% {
      transform: scale(0) rotate(45deg);
    }
    100% {
      transform: scale(1) rotate(45deg);
    }
}

@keyframes showInSimple {
    0% {
      transform: scale(0);
    }
    100% {
      transform: scale(1);
    }
}

